@import url(https://fonts.googleapis.com/css?family=Dancing+Script|Indie+Flower);@import url(https://cdnjs.cloudflare.com/ajax/libs/animateCSS/1.2.2/jquery.animatecss.js);
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  text-align: center;
  -ms-flex-line-pack: center;
      align-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
* {
  padding: 5px;
  font-family: "Indie Flower", cursive;
  font-family: "Dancing Script", cursive;
  text-align: center;
}
html,
body {
  margin: 0;
  padding: 0;
  /* background-color: bisque; */
}
body {
  display: grid;
  background-color: black;
  background-image: url("https://i.imgur.com/5RoPD1Z.jpg");
  background-size: cover;
  height: 1000px;
  z-index: 0;
}
header {
  font-size: 4vw;
  line-height: 0%;
  margin: 0px;
  padding: 0px;
  color: rgb(0, 0, 0);
}
p {
  color: black;
}
footer {
  color: black;
  border: solid black 5px;
  background-color: rgb(255, 228, 196, 0.8);
}
#app > div > h1 {
  position: relative;
}
#home {
  text-align: center;
}
#home > img {
  width: 500px;
  height: 400px;
  /* border: 5px rgb(46, 46, 46) dotted; */
  -webkit-box-shadow: 800px 1200px #555;
          box-shadow: 800px 1200px #555;
  -webkit-box-shadow: 5px 5px 5px black;
          box-shadow: 5px 5px 5px black;
  padding: 0px;
  text-align: center;
}
#forms {
  display: inline-block;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  text-align: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  padding: 20px;
}
#fbutton {
  min-width: 150px;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  text-align: center;
  padding: 5px 15px;
  border: 0 none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 50px;
  width: 20vw;
  max-width: 25vw;
  height: 20vh;
  max-height: 30vw;
  border: 4px darkgrey solid;
  border-radius: 15px;
  color: black;
  background: url("https://i.pinimg.com/originals/73/f0/7a/73f07a3b375055159210d24ffd44e9ec.jpg");
}
#wbutton {
  min-width: 150px;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  text-align: center;
  padding: 5px 15px;
  border: 0 none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 50px;
  width: 20vw;
  max-width: 25vw;
  height: 20vh;
  max-height: 30vw;
  border: 4px darkgrey solid;
  border-radius: 15px;
  color: black;
  background: url("https://i.pinimg.com/originals/73/f0/7a/73f07a3b375055159210d24ffd44e9ec.jpg");
}
.content {
  display: grid;
  text-align: center;
  -ms-flex-line-pack: center;
      align-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
#navigation > a {
  text-decoration: none;
  color: black;
}
#navigation a:hover {
  display: flexbox;
  color: grey;
  -webkit-transition: 500ms;
  transition: 500ms;
  -ms-flex-line-pack: justify;
      align-content: space-between;
  opacity: 50%;
}
#navigation {
  top: 0;
  position: -webkit-sticky;
  position: sticky;
  font-size: 5vw;
  text-decoration: none;
  color: rgb(109, 109, 109);
  border: solid black 5px;
  background-color: rgb(255, 228, 196, 0.8);
}
